          </div>
        </div>
        <div class="col-md-12 row">
          <div class="col-md-2 boardMenu">
            <h4 style="color: rgba(56, 60, 87, 0.7);">Dashboard</h4>           
            <div>
              <router-link class="btnAddUser animate__animated animate__fadeInDown" to="/admin/newuser"><font-awesome-icon icon="plus" /> Add new driver</router-link>
            </div>
            <div class="btnWarning animate__animated animate__fadeInDown" @click="showModal = true">Expired documents</div>
            <div class="btnWarning animate__animated animate__fadeInDown" @click="showModalMissing = true">Missing documents</div>
            <div class="dampFile animate__animated animate__fadeInDown" @click="showExcel()"><font-awesome-icon icon="download" /> Download Excel</div>


          </div>
          <div class="col-md-10 row users">
                <div class="col-md-12 animate__animated animate__headShake" v-for="item in pageOfItems" :key="item._id">
                    <router-link style="text-decoration: none; color: inherit;" :to="'user/' + item._id">
                      <div class="cardUser">
                        <div class="row">
                          <div class="col-lg-12 row">                     
<!--                               <div class="col-md-1 cardUserNumber">
                                  <div class="cardUserNameItem">№</div>
                                  <div>
                                    {{item.number}}
                                  </div>
                              </div> -->
                              <div class="col-lg-12" v-if="!item.firstName || !item.lastName">
                                {{item.username}}
