                                <div>
                                  <font-awesome-icon class="cardUserIco" icon="briefcase" /> <span class="cardUserNameItemSmall"> Company:</span> {{ item.employingCompany}}
                                </div>
                              </div>
                              <div class="col-md-3" v-else>...</div>
                              <div class="col-md-3" v-if="item.dateOfEmplouyment">
                                <div class="cardUserNameItem">
                                  Employment
                                </div>
                                <div>
                                  <font-awesome-icon class="cardUserIco" icon="sign-in-alt" /> <span class="cardUserNameItemSmall">Employment:</span> {{item.dateOfEmplouyment | moment("MM/DD/YYYY") }}
                                </div>
                              </div>
                              <div class="col-md-3" v-else>...</div>
                              <div class="col-md-2 cardUserDriverFault">
                                <div class="cardUserNameItem">
                                  Driver fault
                                </div> 
                                <span class="cardUserNameItemSmall"> Driver fault </span> <span v-for="list in item.safetys" :key="list._id">
                                  <div style="display: inline-block;" v-if="list.driversFault === 'Yes'"> <span class="driverFault" :title=list.description><font-awesome-icon   icon="exclamation" /></span>
