      </div>
    </div>
    <div v-for="item in content" :key="item._id">
      <Countdown :date="item.endOfDateDriverLicense" @onFinish="finish(item._id, item.firstName, item.lastName, item.endOfDateDriverLicense, item.status)"></Countdown>
      <Countdown :date="item.endOfDateMedicalCard" @onFinish="finish2(item._id, item.firstName, item.lastName, item.endOfDateMedicalCard, item.status)"></Countdown>
    </div>
  </div>
</template>

<script> 
const url = 'https://testrole.herokuapp.com/';
// const url = 'http://localhost:3000/';
import Vue from 'vue';
import Toasted from 'vue-toasted';
Vue.config.productionTip = false;
Vue.config.devtools = false;
Vue.use(Toasted);
import axios from 'axios';
import authHeader from '../services/auth-header';
// import UserService from '../services/user.service';
import Countdown from '../components/Countdown';

import JsonExcel from 'vue-json-excel'


export default {
  name: 'Admin',
  components:{
    Countdown, JsonExcel
  },
  data() {
    return {
      fileName:"Drivers.xls",
      json_fields: {},
      jsonExcel: {},
      json: '',
      json_meta: [
        [
          {
            'key': 'charset',
            'value': 'utf-8'
          }
        ]
      ],
      showFilter: false,
      content: [],
      contentSafety: [],
      endDriverLicence: [],
      endMedicalCard: [],
      pageOfItems: [],
      showModal: false,
      showModalMissing: false,
      showModalExcel: false,
      showModalMenu: false,
      selectedCategory: "active",
      selectedCategoryExcel: "true",
      search: '',
      searchExcel: '',
      oldestFirst: false
    };
  },
  computed: {
    sortedByDate() {
      var order = this.oldestFirst ? 1 : -1;   
       return [...this.all].sort(function(a, b) {
        a = new Date(a.date);
        b = new Date(b.date);
        var results = a > b ? -1 : a < b ? 1 : 0;
        return results * order;
      });
    },
    assign(){
      var obj = Object.assign(this.json_fields, this.jsonExcel);
      return obj
    },
    all() {
      var vm = this
      var n = 1
      var selected = "false"
      var result = vm.filteredPeople.map(function(el) {
        var o = Object.assign({}, el);
        o.number = n++;
        o.selected = selected;
        return o;
      })
      return result
    },
    filteredPeople() {
      var vm = this;
      var status = vm.selectedCategory;
      if(status === "All") {
        return vm.content;
      } else {
        return vm.content.filter(function(person) {
          return person.status === status;
        });
      }
    },
    filteredExcel() {
      var vm = this;
      var selected = vm.selectedCategoryExcel;
      if(selected === "All") {
        return vm.all;
      } else {
        return vm.sortedByDate.filter(function(person) {
          return person.selected === selected;
        });
      }
    },
    filteredListExcel() {
      return this.content.filter(list => {
        return list.firstName.toLowerCase().includes(this.searchExcel.toLowerCase()) + list.lastName.toLowerCase().includes(this.searchExcel.toLowerCase())
      })
    },
    filteredList() {
      return this.content.filter(list => {
        return list.firstName.toLowerCase().includes(this.search.toLowerCase()) + list.lastName.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  mounted() {
    // UserService.getAdminBoard().then(
    //   response => {
    //     this.content = response.data
    //   },
    //   error => {
    //     this.content =
    //       (error.response && error.response.data && error.response.data.message) ||
    //       error.message ||
    //       error.toString();
    //   }
    // );
    this.getData();
  },
  methods: {
    toggleSort: function() {
      this.oldestFirst = !this.oldestFirst;
      if(this.oldestFirst){
        Vue.toasted.show('First new records', {
          theme: "toasted-primary",
          position: "top-right",
          duration: 3000,
        });
      }
      else{
        Vue.toasted.show('First old records', {
          theme: "toasted-primary",
          position: "top-right",
          duration: 3000,
        });

      }
    },
    getData() {
      axios
        .get(url + 'admin/', { headers: authHeader()})
          .then(response => {
            return response.data
          })
          .then(data => {
            this.content = data;
          })
          .catch(err => {
             console.log(err)
          })
    },
    addItemtoFilter1(){
      this.jsonExcel = {"First name": 'firstName'}
      this.json_fields = this.assign
    },
    addItemtoFilter2(){
      this.jsonExcel = {"Last name": 'lastName'}
      this.json_fields = this.assign
    },
    addItemtoFilter3(){
      this.jsonExcel = {"Date of birth": 'dateOfBirth'}
      this.json_fields = this.assign
    },
    addItemtoFilter4(){
      this.jsonExcel = {"Social security number": 'socialSecurityNumber'}
      this.json_fields = this.assign
    },
    addItemtoFilter5(){
      this.jsonExcel = {"Driver license number": 'driverLicenseNumber'}
      this.json_fields = this.assign
    },
    addItemtoFilter6(){
      this.jsonExcel = {"Driver license state": 'driverLicenseState'}
      this.json_fields = this.assign
    },
    addItemtoFilter7(){
      this.jsonExcel = {"Driver license issue date": 'driverLicenseIssueDate'}
      this.json_fields = this.assign
    },
    addItemtoFilter8(){
      this.jsonExcel = {"Driver Licence expired date": 'endOfDateDriverLicense'}
      this.json_fields = this.assign
    },
    addItemtoFilter9(){
      this.jsonExcel = {"Home adress": 'homeAdress'}
      this.json_fields = this.assign
    },
    addItemtoFilter10(){
      this.jsonExcel = {"Phone number": 'phoneNumber'}
      this.json_fields = this.assign
    },
    addItemtoFilter11(){
      this.jsonExcel = {"Email": 'email'}
      this.json_fields = this.assign
    },
    addItemtoFilter12(){
      this.jsonExcel = {"Employing company": 'employingCompany'}
      this.json_fields = this.assign
    },
    addItemtoFilter13(){
      this.jsonExcel = {"Date of employment": 'dateOfEmplouyment'}
      this.json_fields = this.assign
    },
    addItemtoFilter14(){
      this.jsonExcel = {"Date of dismissal": 'dateOfDismissal'}
      this.json_fields = this.assign
    },
    addItemtoFilter15(){
      this.jsonExcel = {"Comment": 'comment'}
      this.json_fields = this.assign
    },
    showExcel(){
      this.showModalExcel = true; 
      this.pushJsonFields()
    },
    pushJsonFields(){
      this.json_fields = {
        "First name": 'firstName',
        "Last name": 'lastName',
        "Date of birth": 'dateOfBirth',
        "Social security number": 'socialSecurityNumber',
        "Driver license number": 'driverLicenseNumber',
        "Driver license state": 'driverLicenseState',
        "Driver license issue date": 'driverLicenseIssueDate',
        "Driver Licence expired date": 'endOfDateDriverLicense',
        "Home adress": 'homeAdress',
        "Phone number": 'phoneNumber',
        "Email": 'email',
        "Employing company": 'employingCompany',
        "Date of employment": 'dateOfEmplouyment',
        "Date of dismissal": 'dateOfDismissal',
        "Comment": 'comment'}
    },
    delJsonFields(){
      this.json_fields = {};
      this.jsonExcel = {};
      this.json = {};
    },
    del(i) {
      delete this.json_fields[i];
      this.json = '';
      this.json = this.json_fields;
    },
    addToListExcel: function(id) {
      var el = this.sortedByDate.find(item => item._id === id);
      if (el) {
        el.selected = "true";
        this.searchExcel = '';
      }
    },
    deleteToListExcel: function(id) {
      var el = this.sortedByDate.find(item => item._id === id);
      if (el) {
        el.selected = "false";
      }
    },
    finish(id, firstName, lastName, date, status) {
      setTimeout(() => this.endDriverLicence.push({id: id, firstName: firstName, lastName: lastName, date: date, status: status}), 1000)
    },
    finish2(id, firstName, lastName, date, status) {
      setTimeout(() => this.endMedicalCard.push({id: id, firstName: firstName, lastName: lastName, date: date, status: status}), 1000)
    },
    onChangePage(pageOfItems) {
      this.pageOfItems = pageOfItems;
    },
  }
};
